AAA;;AAElB;EACE,kBAAkB;EAClB,MAAM;EACN,QAAQ;EACR,eAAe;EACf,gBAAgB;EAChB,gBAAgB;EAChB,iBAAiB;EACjB,wBAAwB;EACxB,UAAU;EACV,aAAa;EACb,iBAAiB;EACjB,eAAe;EACf,cAAc,EAAA;;AAGhB;EACE,oCAAmC,EAAA;;AAGrC,sBAAA;AAEA;EACE,YAAY;EACZ,gCAAgC;EAChC,gBAAgB,EAAA;;AAGlB,4BAAA;AAEA;EACE,yBAAyB;EACzB,YAAY,EAAA;;AAGd;EACE,yBAAyB;EACzB,cAAc,EAAA;;AAGhB;EACE,yBAAyB;EACzB,YAAY,EAAA;;AAGd;EACE,yBAAyB;EACzB,YAAY,EAAA;;AAGd,yCAAA;AACA;;EAEE,yBAAyB;EACzB,YAAY,EAAA;;AAGd,+BAAA;AACA;EACE,yBAAyB,EAAA;;AAG3B;;EAEE,eAAe,EAAA;;AAGjB,2BAAA;AACA;EACE,cAAc;EACd,aAAa,EAAA;;AAGf;EACE,yBAAyB,EAAA;;AAG3B;EACE,gBAAgB;EAChB,mBAAmB,EAAA;;AAGrB,+BAAA;AACA;EACE,cAAc,EAAA;;AAGhB;EACE,YAAY,EAAA","sourcesContent":["\n// Override bootstrap related variables\n@import 'variables';\n\n@import '../../node_modules/bootstrap/scss/bootstrap';\n@import '../../node_modules/mdbootstrap/scss/mdb';\n@import '../../node_modules/@fortawesome/fontawesome-free/scss/fontawesome';\n@import '../../node_modules/@fortawesome/fontawesome-free/scss/brands';\n@import '../../node_modules/@fortawesome/fontawesome-free/scss/regular';\n@import '../../node_modules/@fortawesome/fontawesome-free/scss/solid';\n@import '../../node_modules/selectize-bootstrap4-theme/scss/selectize.bootstrap4';\n@import '../../node_modules/tempusdominus-bootstrap-4/src/sass/tempusdominus-bootstrap-4';\n@import '../../node_modules/tags-input/tags-input.css';\n@import '../../node_modules/bs-stepper/dist/css/bs-stepper.css';\n@import '../../node_modules/datatables.net-bs4/css/dataTables.bootstrap4.css';\n\n// Extend selectize bootstrap theme\n.selectize-control {\n  @extend .selectize-control;\n\n  .option {\n    .description {\n      color: $gray-600;\n      display: block;\n      font-size: $font-size-sm;\n    }\n\n    &:hover {\n      .description {\n        color: $selectize-dropdown-option-color-active;\n      }\n    }\n\n    &.active,\n    &:active {\n      .description {\n        color: $selectize-dropdown-option-color-active;\n      }\n    }\n  }\n}\n\n// Search input on DataTable\n.dataTables_filter input {\n  background-color: transparent;\n  border: 0;\n  border-bottom: 1px solid $input-border-color;\n  border-radius: 0;\n  box-shadow: none;\n  box-sizing: content-box;\n  color: $input-color;\n  font-family: inherit;\n  font-size: $input-label-font-size;\n  outline: none;\n\n  // Focused input style\n  &:focus:not([readonly]) {\n    border-bottom: 1px solid $input-md-focus-color;\n    box-shadow: 0 1px 0 0 $input-md-focus-color;\n    // Focused label style\n    + label {\n      color: $input-md-focus-color;\n    }\n  }\n}\n\n// Length select on DataTable\n.dataTables_length {\n  border: 0;\n  position: relative;\n\n  .custom-select {\n    // Remove arrow on Firefox\n    -moz-appearance: none;\n    // Remove arrow on Chrome\n    -webkit-appearance: none;\n    appearance: none;\n    background: transparent;\n    border: 0;\n    border-bottom: 1px solid $input-border-color;\n    color: $input-color;\n\n    // Remove arrow on IE10\n    &::-ms-expand {\n      display: none;\n    }\n\n    &:focus {\n      outline: none;\n    }\n  }\n}\n\n.md-select {\n  // Remove arrow on Firefox\n  -moz-appearance: none;\n  // Remove arrow on Chrome\n  -webkit-appearance: none;\n  background-color: transparent;\n  border: 0;\n  border-bottom: 1px solid $input-border-color;\n  color: $input-color;\n  font-family: inherit;\n  font-size: $input-label-font-size;\n  padding: $input-form-control-padding-top 0 $input-form-control-padding-bottom;\n  width: 100%;\n\n  // Remove arrow on IE10\n  &::-ms-expand {\n    display: none;\n  }\n\n  &:focus {\n    outline: none;\n  }\n}\n\n.md-selectfield {\n  border: 0;\n  position: relative;\n\n  label {\n    color: $input-label-color;\n    cursor: text;\n    font-size: $label-font-size;\n    left: 0;\n    position: absolute;\n    top: $input-label-top;\n    transform: $input-label-active-transform;\n    transition: $input-label-transition;\n  }\n\n  .md-select {\n    appearance: none;\n  }\n\n  &::after {\n    border-left: .25em solid transparent;\n    border-right: .25em solid transparent;\n    border-top: .375em solid $black;\n    content: '';\n    height: 0;\n    padding: 0;\n    pointer-events: none;\n    position: absolute;\n    right: .5em;\n    top: .75em;\n    width: 0;\n  }\n}\n\n// Bell notifications\n.bell-notification {\n  padding: 0.6em;\n  font-size: 1.0em;\n}\n\n.bell-counter::after {\n  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3);\n  font-size: 1.6em;\n  font-weight: 700;\n  position: absolute;\n  top: 30px;\n  left: 18px;\n  padding: 5px 8px;\n  line-height: 100%;\n  border-radius: 60px;\n  background: #d00;\n  content: attr(count);\n  -webkit-transform: scale(0.5);\n  transform: scale(0.5);\n  transition: transform, opacity;\n  transition-duration: 0.3s;\n  transition-timing-function: ease-out;\n}\n\n// Refresh button on index page\n.device-row-refresher > .fas:active,\n#refresh-table-content > .fas:active {\n  color: $primary-color;\n}\n\n// Device rows on index table\n.selectable-device-row {\n\n  cursor: pointer;\n}\n\n.not-selectable-device-row:hover {\n\n  background-color: transparent !important;\n}\n\n.hover-effect:hover {\n\n  color: $primary-color-dark;\n}\n\n.dropdown-menu.dropdown-menu-inline.show {\n  display: flex;\n  flex-direction: row;\n}\n\n.md-selectfield.md-selectfield-vlan {\n  padding: 0;\n}\n\n.md-select.md-select-vlan {\n  padding : 0.6rem 1.2rem 0.4rem 0rem;\n}\n\n@media (max-width: 992px) {\n  .container {\n    width: 100%;\n    max-width: none;\n  }\n}\n\n.font-bold {\n  font-weight: bold;\n}\n\n.flash {\n  margin-top: 20px;\n}\n\n.version-label {\n  font-size: 10px;\n}\n\n.md-addon {\n  background-color: transparent;\n  border-color: transparent;\n}\n\n/* Rounded sliders */\n\n.switch {\n  position: relative;\n  display: inline-block;\n  width: 60px;\n  height: 34px;\n}\n\n.switch input {display:none;}\n\n.slider {\n  position: absolute;\n  cursor: pointer;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: #ccc;\n  -webkit-transition: .4s;\n  transition: .4s;\n}\n\n.slider:before {\n  position: absolute;\n  content: \"\";\n  height: 26px;\n  width: 26px;\n  left: 4px;\n  bottom: 4px;\n  background-color: white;\n  -webkit-transition: .4s;\n  transition: .4s;\n}\n\ninput:checked + .slider {\n  background-color: #4db6ac;\n}\n\ninput:focus + .slider {\n  box-shadow: 0 0 1px #4db6ac;\n}\n\ninput:checked + .slider:before {\n  -webkit-transform: translateX(26px);\n  -ms-transform: translateX(26px);\n  transform: translateX(26px);\n}\n\n.slider.round {\n  border-radius: 34px;\n}\n\n.slider.round:before {\n  border-radius: 50%;\n}\n\n/* File input */\n\n.btn-file {\n  position: relative;\n  overflow: hidden;\n}\n.btn-file input[type=file] {\n  position: absolute;\n  top: 0;\n  right: 0;\n  min-width: 100%;\n  min-height: 100%;\n  font-size: 100px;\n  text-align: right;\n  filter: alpha(opacity=0);\n  opacity: 0;\n  outline: none;\n  background: white;\n  cursor: inherit;\n  display: block;\n}\n\n.flashbox-color {\n  background-color: #4db6ac!important;\n}\n\n/* tags search input */\n\n.tags-input {\n  border: none;\n  border-bottom: 1px solid #ced4da;\n  box-shadow: none;\n}\n\n/* nav pills custom colors */\n\n.nav-pills .nav-link:not(.active) {\n  background-color: #4db6ac;\n  color: white;\n}\n\n.nav-pills .nav-link:hover {\n  background-color: #4db6ac;\n  color: #2c6b65; \n}\n\n.nav-pills .nav-link:focus {\n  background-color: #3f968e;\n  color: white;\n}\n\n.nav-pills .nav-link.active {\n  background-color: #3f968e;\n  color: white;\n}\n\n/* pagination used by dataTables plugin */\n.pagination .page-item.active .page-link,\n.pagination .page-item.active .page-link:hover {\n  background-color: #4db6ac;\n  color: white;\n}\n\n/* modal dialog customization */\n.modal-dialog.modal-notify.modal-teal .modal-header {\n  background-color: #4db6ac;\n}\n\n.fa-chevron-down::before,\n.fa-chevron-up::before {\n  cursor: pointer;\n}\n\n/* Stepper color override */\n.bs-stepper .step-trigger:focus {\n  color: #3f968e;\n  outline: none;\n}\n\n.active .bs-stepper-circle {\n  background-color: #4db6ac;\n}\n\n.bs-stepper .step-trigger {\n  padding-top: 0px;\n  padding-bottom: 0px;\n}\n\n/* Time picker color override */\n.table-condensed a > .fa {\n  color: #4db6ac;\n}\n\n.input-group-text.remove-border {\n  border: none;\n}\n\n","/*!\n * Bootstrap v4.3.1 (https://getbootstrap.com/)\n * Copyright 2011-2019 The Bootstrap Authors\n * Copyright 2011-2019 Twitter, Inc.\n * Licensed under MIT (https://github.com/twbs/bootstrap/blob/master/LICENSE)\n */\n\n@import \"functions\";\n@import \"variables\";\n@import \"mixins\";\n@import \"root\";\n@import \"reboot\";\n@import \"type\";\n@import \"images\";\n@import \"code\";\n@import \"grid\";\n@import \"tables\";\n@import \"forms\";\n@import \"buttons\";\n@import \"transitions\";\n@import \"dropdown\";\n@import \"button-group\";\n@import \"input-group\";\n@import \"custom-forms\";\n@import \"nav\";\n@import \"navbar\";\n@import \"card\";\n@import \"breadcrumb\";\n@import \"pagination\";\n@import \"badge\";\n@import \"jumbotron\";\n@import \"alert\";\n@import \"progress\";\n@import \"media\";\n@import \"list-group\";\n@import \"close\";\n@import \"toasts\";\n@import \"modal\";\n@import \"tooltip\";\n@import \"popover\";\n@import \"carousel\";\n@import \"spinners\";\n@import \"utilities\";\n@import \"print\";\n",":root {\n  // Custom variable values only support SassScript inside `#{}`.\n  @each $color, $value in $colors {\n    --#{$color}: #{$value};\n  }\n\n  @each $color, $value in $theme-colors {\n    --#{$color}: #{$value};\n  }\n\n  @each $bp, $value in $grid-breakpoints {\n    --breakpoint-#{$bp}: #{$value};\n  }\n\n  // Use `inspect` for lists so that quoted items keep the quotes.\n  // See https://github.com/sass/sass/issues/2383#issuecomment-336349172\n  --font-family-sans-serif: #{inspect($font-family-sans-serif)};\n  --font-family-monospace: #{inspect($font-family-monospace)};\n}\n","// stylelint-disable at-rule-no-vendor-prefix, declaration-no-important, selector-no-qualifying-type, property-no-vendor-prefix\n\n// Reboot\n//\n// Normalization of HTML elements, manually forked from Normalize.css to remove\n// styles targeting irrelevant browsers while applying new styles.\n//\n// Normalize is licensed MIT. https://github.com/necolas/normalize.css\n\n\n// Document\n//\n// 1. Change from `box-sizing: content-box` so that `width` is not affected by `padding` or `border`.\n// 2. Change the default font family in all browsers.\n// 3. Correct the line height in all browsers.\n// 4. Prevent adjustments of font size after orientation changes in IE on Windows Phone and in iOS.\n// 5. Change the default tap highlight to be completely transparent in iOS.\n\n*,\n*::before,\n*::after {\n  box-sizing: border-box; // 1\n}\n\nhtml {\n  font-family: sans-serif; // 2\n  line-height: 1.15; // 3\n  -webkit-text-size-adjust: 100%; // 4\n  -webkit-tap-highlight-color: rgba($black, 0); // 5\n}\n\n// Shim for \"new\" HTML5 structural elements to display correctly (IE10, older browsers)\n// TODO: remove in v5\n// stylelint-disable-next-line selector-list-comma-newline-after\narticle, aside, figcaption, figure, footer, header, hgroup, main, nav, section {\n  display: block;\n}\n\n// Body\n//\n// 1. Remove the margin in all browsers.\n// 2. As a best practice, apply a default `background-color`.\n// 3. Set an explicit initial text-align value so that we can later use\n//    the `inherit` value on things like `<th>` elements.\n\nbody {\n  margin: 0; // 1\n  font-family: $font-family-base;\n  @include font-size($font-size-base);\n  font-weight: $font-weight-base;\n  line-height: $line-height-base;\n  color: $body-color;\n  text-align: left; // 3\n  background-color: $body-bg; // 2\n}\n\n// Suppress the focus outline on elements that cannot be accessed via keyboard.\n// This prevents an unwanted focus outline from appearing around elements that\n// might still respond to pointer events.\n//\n// Credit: https://github.com/suitcss/base\n[tabindex=\"-1\"]:focus {\n  outline: 0 !important;\n}\n\n\n// Content grouping\n//\n// 1. Add the correct box sizing in Firefox.\n// 2. Show the overflow in Edge and IE.\n\nhr {\n  box-sizing: content-box; // 1\n  height: 0; // 1\n  overflow: visible; // 2\n}\n\n\n//\n// Typography\n//\n\n// Remove top margins from headings\n//\n// By default, `<h1>`-`<h6>` all receive top and bottom margins. We nuke the top\n// margin for easier control within type scales as it avoids margin collapsing.\n// stylelint-disable-next-line selector-list-comma-newline-after\nh1, h2, h3, h4, h5, h6 {\n  margin-top: 0;\n  margin-bottom: $headings-margin-bottom;\n}\n\n// Reset margins on paragraphs\n//\n// Similarly, the top margin on `<p>`s get reset. However, we also reset the\n// bottom margin to use `rem` units instead of `em`.\np {\n  margin-top: 0;\n  margin-bottom: $paragraph-margin-bottom;\n}\n\n// Abbreviations\n//\n// 1. Duplicate behavior to the data-* attribute for our tooltip plugin\n// 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.\n// 3. Add explicit cursor to indicate changed behavior.\n// 4. Remove the bottom border in Firefox 39-.\n// 5. Prevent the text-decoration to be skipped.\n\nabbr[title],\nabbr[data-original-title] { // 1\n  text-decoration: underline; // 2\n  text-decoration: underline dotted; // 2\n  cursor: help; // 3\n  border-bottom: 0; // 4\n  text-decoration-skip-ink: none; // 5\n}\n\naddress {\n  margin-bottom: 1rem;\n  font-style: normal;\n  line-height: inherit;\n}\n\nol,\nul,\ndl {\n  margin-top: 0;\n  margin-bottom: 1rem;\n}\n\nol ol,\nul ul,\nol ul,\nul ol {\n  margin-bottom: 0;\n}\n\ndt {\n  font-weight: $dt-font-weight;\n}\n\ndd {\n  margin-bottom: .5rem;\n  margin-left: 0; // Undo browser default\n}\n\nblockquote {\n  margin: 0 0 1rem;\n}\n\nb,\nstrong {\n  font-weight: $font-weight-bolder; // Add the correct font weight in Chrome, Edge, and Safari\n}\n\nsmall {\n  @include font-size(80%); // Add the correct font size in all browsers\n}\n\n//\n// Prevent `sub` and `sup` elements from affecting the line height in\n// all browsers.\n//\n\nsub,\nsup {\n  position: relative;\n  @include font-size(75%);\n  line-height: 0;\n  vertical-align: baseline;\n}\n\nsub { bottom: -.25em; }\nsup { top: -.5em; }\n\n\n//\n// Links\n//\n\na {\n  color: $link-color;\n  text-decoration: $link-decoration;\n  background-color: transparent; // Remove the gray background on active links in IE 10.\n\n  @include hover {\n    color: $link-hover-color;\n    text-decoration: $link-hover-decoration;\n  }\n}\n\n// And undo these styles for placeholder links/named anchors (without href)\n// which have not been made explicitly keyboard-focusable (without tabindex).\n// It would be more straightforward to just use a[href] in previous block, but that\n// causes specificity issues in many other styles that are too complex to fix.\n// See https://github.com/twbs/bootstrap/issues/19402\n\na:not([href]):not([tabindex]) {\n  color: inherit;\n  text-decoration: none;\n\n  @include hover-focus {\n    color: inherit;\n    text-decoration: none;\n  }\n\n  &:focus {\n    outline: 0;\n  }\n}\n\n\n//\n// Code\n//\n\npre,\ncode,\nkbd,\nsamp {\n  font-family: $font-family-monospace;\n  @include font-size(1em); // Correct the odd `em` font sizing in all browsers.\n}\n\npre {\n  // Remove browser default top margin\n  margin-top: 0;\n  // Reset browser default of `1em` to use `rem`s\n  margin-bottom: 1rem;\n  // Don't allow content to break outside\n  overflow: auto;\n}\n\n\n//\n// Figures\n//\n\nfigure {\n  // Apply a consistent margin strategy (matches our type styles).\n  margin: 0 0 1rem;\n}\n\n\n//\n// Images and content\n//\n\nimg {\n  vertical-align: middle;\n  border-style: none; // Remove the border on images inside links in IE 10-.\n}\n\nsvg {\n  // Workaround for the SVG overflow bug in IE10/11 is still required.\n  // See https://github.com/twbs/bootstrap/issues/26878\n  overflow: hidden;\n  vertical-align: middle;\n}\n\n\n//\n// Tables\n//\n\ntable {\n  border-collapse: collapse; // Prevent double borders\n}\n\ncaption {\n  padding-top: $table-cell-padding;\n  padding-bottom: $table-cell-padding;\n  color: $table-caption-color;\n  text-align: left;\n  caption-side: bottom;\n}\n\nth {\n  // Matches default `<td>` alignment by inheriting from the `<body>`, or the\n  // closest parent with a set `text-align`.\n  text-align: inherit;\n}\n\n\n//\n// Forms\n//\n\nlabel {\n  // Allow labels to use `margin` for spacing.\n  display: inline-block;\n  margin-bottom: $label-margin-bottom;\n}\n\n// Remove the default `border-radius` that macOS Chrome adds.\n//\n// Details at https://github.com/twbs/bootstrap/issues/24093\nbutton {\n  // stylelint-disable-next-line property-blacklist\n  border-radius: 0;\n}\n\n// Work around a Firefox/IE bug where the transparent `button` background\n// results in a loss of the default `button` focus styles.\n//\n// Credit: https://github.com/suitcss/base/\nbutton:focus {\n  outline: 1px dotted;\n  outline: 5px auto -webkit-focus-ring-color;\n}\n\ninput,\nbutton,\nselect,\noptgroup,\ntextarea {\n  margin: 0; // Remove the margin in Firefox and Safari\n  font-family: inherit;\n  @include font-size(inherit);\n  line-height: inherit;\n}\n\nbutton,\ninput {\n  overflow: visible; // Show the overflow in Edge\n}\n\nbutton,\nselect {\n  text-transform: none; // Remove the inheritance of text transform in Firefox\n}\n\n// Remove the inheritance of word-wrap in Safari.\n//\n// Details at https://github.com/twbs/bootstrap/issues/24990\nselect {\n  word-wrap: normal;\n}\n\n\n// 1. Prevent a WebKit bug where (2) destroys native `audio` and `video`\n//    controls in Android 4.\n// 2. Correct the inability to style clickable types in iOS and Safari.\nbutton,\n[type=\"button\"], // 1\n[type=\"reset\"],\n[type=\"submit\"] {\n  -webkit-appearance: button; // 2\n}\n\n// Opinionated: add \"hand\" cursor to non-disabled button elements.\n@if $enable-pointer-cursor-for-buttons {\n  button,\n  [type=\"button\"],\n  [type=\"reset\"],\n  [type=\"submit\"] {\n    &:not(:disabled) {\n      cursor: pointer;\n    }\n  }\n}\n\n// Remove inner border and padding from Firefox, but don't restore the outline like Normalize.\nbutton::-moz-focus-inner,\n[type=\"button\"]::-moz-focus-inner,\n[type=\"reset\"]::-moz-focus-inner,\n[type=\"submit\"]::-moz-focus-inner {\n  padding: 0;\n  border-style: none;\n}\n\ninput[type=\"radio\"],\ninput[type=\"checkbox\"] {\n  box-sizing: border-box; // 1. Add the correct box sizing in IE 10-\n  padding: 0; // 2. Remove the padding in IE 10-\n}\n\n\ninput[type=\"date\"],\ninput[type=\"time\"],\ninput[type=\"datetime-local\"],\ninput[type=\"month\"] {\n  // Remove the default appearance of temporal inputs to avoid a Mobile Safari\n  // bug where setting a custom line-height prevents text from being vertically\n  // centered within the input.\n  // See https://bugs.webkit.org/show_bug.cgi?id=139848\n  // and https://github.com/twbs/bootstrap/issues/11266\n  -webkit-appearance: listbox;\n}\n\ntextarea {\n  overflow: auto; // Remove the default vertical scrollbar in IE.\n  // Textareas should really only resize vertically so they don't break their (horizontal) containers.\n  resize: vertical;\n}\n\nfieldset {\n  // Browsers set a default `min-width: min-content;` on fieldsets,\n  // unlike e.g. `<div>`s, which have `min-width: 0;` by default.\n  // So we reset that to ensure fieldsets behave more like a standard block element.\n  // See https://github.com/twbs/bootstrap/issues/12359\n  // and https://html.spec.whatwg.org/multipage/#the-fieldset-and-legend-elements\n  min-width: 0;\n  // Reset the default outline behavior of fieldsets so they don't affect page layout.\n  padding: 0;\n  margin: 0;\n  border: 0;\n}\n\n// 1. Correct the text wrapping in Edge and IE.\n// 2. Correct the color inheritance from `fieldset` elements in IE.\nlegend {\n  display: block;\n  width: 100%;\n  max-width: 100%; // 1\n  padding: 0;\n  margin-bottom: .5rem;\n  @include font-size(1.5rem);\n  line-height: inherit;\n  color: inherit; // 2\n  white-space: normal; // 1\n}\n\nprogress {\n  vertical-align: baseline; // Add the correct vertical alignment in Chrome, Firefox, and Opera.\n}\n\n// Correct the cursor style of increment and decrement buttons in Chrome.\n[type=\"number\"]::-webkit-inner-spin-button,\n[type=\"number\"]::-webkit-outer-spin-button {\n  height: auto;\n}\n\n[type=\"search\"] {\n  // This overrides the extra rounded corners on search inputs in iOS so that our\n  // `.form-control` class can properly style them. Note that this cannot simply\n  // be added to `.form-control` as it's not specific enough. For details, see\n  // https://github.com/twbs/bootstrap/issues/11586.\n  outline-offset: -2px; // 2. Correct the outline style in Safari.\n  -webkit-appearance: none;\n}\n\n//\n// Remove the inner padding in Chrome and Safari on macOS.\n//\n\n[type=\"search\"]::-webkit-search-decoration {\n  -webkit-appearance: none;\n}\n\n//\n// 1. Correct the inability to style clickable types in iOS and Safari.\n// 2. Change font properties to `inherit` in Safari.\n//\n\n::-webkit-file-upload-button {\n  font: inherit; // 2\n  -webkit-appearance: button; // 1\n}\n\n//\n// Correct element displays\n//\n\noutput {\n  display: inline-block;\n}\n\nsummary {\n  display: list-item; // Add the correct display in all browsers\n  cursor: pointer;\n}\n\ntemplate {\n  display: none; // Add the correct display in IE\n}\n\n// Always hide an element with the `hidden` HTML attribute (from PureCSS).\n// Needed for proper display in IE 10-.\n[hidden] {\n  display: none !important;\n}\n","// Variables\n//\n// Variables should follow the `$component-state-property-size` formula for\n// consistent naming. Ex: $nav-link-disabled-color and $modal-content-box-shadow-xs.\n\n// Color system\n\n$white:    #fff !default;\n$gray-100: #f8f9fa !default;\n$gray-200: #e9ecef !default;\n$gray-300