r-color;\n        content: attr(data-error);\n        opacity: 1;\n      }\n\n      &.form-control.valid + label:after,\n      &.form-control:focus.valid + label:after {\n        position: absolute;\n        top: 4rem;\n        left: 0;\n      }\n\n      &.form-control.invalid + label:after,\n      &.form-control:focus.invalid + label:after {\n        position: absolute;\n        top: 4rem;\n        left: 0;\n      }\n    }\n\n    > input[type]:-webkit-autofill:not(.browser-default):not([type=\"search\"]) + label,\n    > input[type=\"time\"]:not(.browser-default) + label {\n      left: 8px;\n      padding-right: 5px;\n      padding-left: 5px;\n      font-size: 1rem;\n      font-weight: 500;\n      background: #fff;\n      transform: translateY(-9px) scale(.8);\n      transform-origin: 0 0;\n    }\n\n    > input[type]:-webkit-autofill:not(.browser-default):not([type=\"search\"]) + label.active,\n    > input[type=\"time\"]:not(.browser-default) + label.active {\n      transform: translateY(-9px) scale(.8);\n      transform-origin: 0 0;\n    }\n\n    @-webkit-keyframes autofill {\n      to {\n        color: #495057;\n        background: transparent;\n      }\n    }\n\n    @keyframes autofill {\n      to {\n        color: #495057;\n        background: transparent;\n      }\n    }\n\n    input:-webkit-autofill {\n      -webkit-animation-name: autofill;\n      animation-name: autofill;\n      -webkit-animation-fill-mode: both;\n      animation-fill-mode: both;\n    }\n\n    .form-control {\n      padding: .375rem .75rem;\n    }\n\n    label {\n      position: absolute;\n      top: 0;\n      left: 0;\n      padding-left: 10px;\n      font-size: 1rem;\n      color: #757575;\n      cursor: text;\n      transition: transform .2s ease-out, color .2s ease-out;\n      transform: translateY(9px);\n      transform-origin: 0% 100%;\n\n      &.active {\n        left: 8px;\n        padding-right: 5px;\n        padding-left: 5px;\n        font-weight: 500;\n        background: #fff;\n